.hownd-news .news-wrapper {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  padding: 10px;
  background-color: #fafafa;
}
.hownd-news .item {
  width: 25%;
}
.hownd-news .wrapper {
  padding: 10px;
}
.hownd-news .news-link {
  display: flex;
}
.hownd-news .news-title {
  padding: 0 5px;
  margin: 20px 0 15px;
  font-weight: 900;
  font-size: 16px;
  text-align: center;
}
.hownd-news .news-description {
  padding: 0 5px;
  margin: 0 0 20px;
  line-height: 1.15;
  font-size: 16px;
  text-align: center;
}
@media (max-width: 1199px) {
  .hownd-news .item {
    width: 33.33%;
  }
}
@media (max-width: 991px) {
  .hownd-news .item {
    width: 50%;
  }
}
@media (max-width: 575px) {
  .hownd-news .item {
    width: 100%;
  }
}